

























export default {
	data() {
	    return {
	        username: '',
	        password: '',
	        email: '',
	    }
	},
	methods:{
			Register() {
				var email = this.email;
				var username = this.username;
				var password = this.password;
				
				if (email.length < 1 || !~email.indexOf('@')) {
				    uni.showToast({
				        icon: 'none',
				        title: 'Your email address is not valid'
				    });
				    return;
				}
				if (username.length < 1) {
				    uni.showToast({
				        icon: 'none',
				        title: 'Your name is not valid'
				    });
				    return;
				}
				if (password.length < 1) {
				    uni.showToast({
				        icon: 'none',
				        title: 'Your password is not valid'
				    });
				    return;
				}
				if (password != this.v_password) {
				    uni.showToast({
				        icon: 'none',
				        title: 'please type same password while vertify'
				    });
				    return;
				}
				
				var ServerUrl = this.$serverUrl;
				
			uni.request({
				url: ServerUrl + '/register',
				method: 'POST',
				data: {
					username: username,
					password: password,
					email: email
				},
				header:{
					'content-type': 'application/json'
				},
		
				success: (res) => {
					console.log(res.data);
					var status = res.data.status;
					if(status == 200){
						uni.showToast({
								icon: 'none',
								title: 'Register Complete'
							});
							uni.navigateTo({
								url: '../login/login',
							});
					}else if(status == 500){
						uni.showToast({
								icon: 'none',
								title: res.data.msg,
								duration: 3000
							});
					}
					
					},fail: (res) => {
						console.log(res.data);
						uni.showToast({
							icon: 'none',
							title: res.data.msg,
						});
				}
			});
		}
	},
	
	/*onLoad: function() {
		uni.setNavigationBarTitle({
			title: "Sign up"
		});
	}*/
	
}
